import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";
import React from 'react'
import './Banner.css'


const Banner = () => {
  const particlesInit = async (main) => {
    console.log(main);

    // you can initialize the tsParticles instance (main) here, adding custom shapes or presets
    // this loads the tsparticles package bundle, it's the easiest method for getting everything ready
    // starting from v2 you can add only the features you need reducing the bundle size
    await loadFull(main);
  };
  return (
    <>
    
     <header>
     <Particles
      id="tsparticles"
      init={particlesInit}
      // loaded={particlesLoaded}
      options={{
        background: {
          color: {
            value: "transparent",
          },
        },
        fpsLimit: 120,
        interactivity: {
          events: {
            onClick: {
              enable: true,
              mode: "push",
            },
            onHover: {
              enable: true,
              mode: "repulse",
            },
            resize: true,
          },
          modes: {
            push: {
              quantity: 4,
            },
            repulse: {
              distance: 200,
              duration: 0.4,
            },
          },
        },
        particles: {
          color: {
            value: ["#ffffff","#ff7979","#badc58"]
          },
          links: {
            color: "false",
            distance: 150,
            enable: true,
            opacity: 0.5,
            width: 1,
          },
          collisions: {
            enable: true,
          },
          move: {
            direction: "top",
            enable: true,
            // outModes: {
            //   default: "bounce",
            // },
            random: false,
            speed: 6,
            straight: false,
            outMode:"out",
            attract:{
              enable:"false",
              rotateX: 600,
              rotateY: 1200
            }
          },
          number: {
            density: {
              enable: true,
              area: 800,
            },
            value: 80,
          },
          opacity: {
            value: 1,
          },
          shape: {
            type: ["circle","star"]
          },
          size: {
            value: { min: 1, max: 4 },
          },
        },
        detectRetina: true,
      }}
    />
  );
         <h1>Best Paint <br />And Colors</h1>
         <button className='blue-btn'>Shop now</button>
     </header>
    </>
  )
}

export default Banner